import React, { useEffect } from 'react'
import Mason from '@gregarcher/masonry'

import './App.css';

const hipsum = `Taxidermy proident hoodie brooklyn PBR&B godard succulents actually. Vice exercitation banh mi kombucha sed squid. Aliqua mumblecore raw denim pitchfork, intelligentsia in blog tote bag glossier normcore vice sartorial narwhal dolore echo park. Irony heirloom do, subway tile XOXO gluten-free magna. Normcore pok pok seitan hella roof party iceland humblebrag disrupt lumbersexual flexitarian mumblecore fingerstache helvetica. Yr laboris iceland, wayfarers proident hell of kitsch tilde palo santo dreamcatcher cupidatat gastropub ea. Tbh sriracha crucifix jianbing semiotics typewriter in et migas tacos.`
const colors = [
  '#FF8800', '#FF0088', '#88ff00', '#8800ff',
  '#00ff88', '#0088ff', '#FFAE00', '#D4FF00',
  '#91D900', '#10BE10', '#10BE84', '#10DCDC',
  '#108FFF', '#9875FF', '#D666FF', '#FF38CA',
  '#FF5500', '#e6cc95', '#e5f3a2', '#91aa73',
  '#518851', '#7ab29f', '#a3e4e4', '#94bde1',
  '#9d84d0', '#c096e2', '#d190c0', '#be806c',
  '#cccccc',  '#aaaaaa', '#777777', '#555555'
]
const randomEntries = new Array(80).fill('.').map(() => {
  const textStart = parseInt(Math.random() * 400)
  const height = parseInt(Math.random() * 300) + 100
  const text = hipsum.slice(textStart, textStart + (height / 5))
  const color = colors[Math.floor(Math.random() * colors.length)].split('#')[1]
  return <div className='random-entry'>
    <div className='entry-content'>
      <img src={`https://dummyimage.com/300x${height}/${color}.png&text=+`} alt='colors!'/>
      <p>{text}</p>
    </div>
  </div>
})

const randomColors = new Array(80).fill('.').map(() => {
  const height = parseInt(Math.random() * 300) + 100
  const color = colors[Math.floor(Math.random() * colors.length)].split('#')[1]
  return <img style={{display: 'block'}} src={`https://dummyimage.com/300x${height}/${color}.png&text=+`} alt='colors!'/>
})

function App() {
  const columnConfig = {
    "small": {
      "query": "(max-width: 720px)",
      "columns": 2
    },
    "medium": {
      "query": "(min-width: calc(720px + 1px)) and (max-width: calc(1023px - 1px) )",
      "columns": 3
    },
    "large": {
      "query": "(min-width: 1023px)",
      "columns": 4
    }
  }
  
  const staticColumn = {
    "default": {
      "columns": 3
    }
  }
  
  useEffect(() => {
    // window.setTimeout(() => setEntries(randomEntries.slice(0,8)), 3000)
    // window.setTimeout(() => setEntries(randomEntries.slice(0,12)), 6000)
    // window.setTimeout(() => setEntries(randomEntries.slice(0,16)), 9000)
  }, [])
  
  
  return (
    <div className="App">
      <h1>Responsive</h1>
      <h2>HTML children with gutter</h2>
      <Mason columns={columnConfig}>{randomEntries.slice(0, 16)}</Mason>
      
      <h2>Plain image children, no gutter</h2>
      <Mason columns={columnConfig}>{randomColors.slice(0, 16)}</Mason>
  
      <h1>Static</h1>
      <Mason columns={staticColumn}>{randomEntries.slice(0, 16)}</Mason>

    </div>
  );
}

export default App;
